        /* Sidebar */
        
        .sidebarnav {
            @include sidebar-menu(100%, 16%);
            height: auto !important;
            /* only for demonstration, should be removed */
        }
        
        .sidebarnav ul {
            list-style-type: none;
            padding: 0;
